import { useForm } from 'react-hook-form';
import styled from 'styled-components';
import useBankStore from '../hooks/useBankStore';
import numberFormat from '../utils/numberFormat';
import PrimaryButton from './ui/PrimaryButton';

const Container = styled.div`
  color: ${(props) => props.theme.colors.contentText};
  padding-inline: calc((100% - 300px) / 2);
  padding-block: calc((100% - 1000px) / 2);
 
  display: flex;
  flex-direction: column;
`;

const Title = styled.h2`
  text-align: center;
  font-weight: bold;
  font-size: 2em;
  color: ${(props) => props.theme.colors.titleText};
  margin-bottom: 1em;
  padding-bottom: .5em;
  border-bottom: 1px solid #A79FFF;
`;

const Element = styled.div`
  margin: 1.8em 0;
`;

const Label = styled.label`
  display: block;
  font-size: .9em;
  font-weight: bold;
  margin-bottom: .5em;
`;

const Field = styled.input`
  padding-block: .8em;
  padding-inline: .5em;
  border: 1px solid #d2d2d2;
  width: 100%;
`;

const DefaultMeesage = styled.p`
  font-size: .9em;
  margin-top: .5em;
`;

const Error = styled.div`
  font-size: .9em;
  color: #F00;
  margin-top: .5em;
`;

export default function TransferForm() {
  const bankStore = useBankStore();

  const {
    register, handleSubmit, formState: { errors },
  } = useForm({ reValidateMode: 'onSubmit' });

  const onSubmit = async (data) => {
    const { accountNumber, amount, name } = data;

    await bankStore.requestTransfer({ to: accountNumber, amount, name });
  };

  return (
    <Container>
      <form onSubmit={handleSubmit(onSubmit)}>
        <Title></Title>
        <Element>
          <Label htmlFor="input-account">   &#58;</Label>
          <Field
            id="input-account"
            // eslint-disable-next-line react/jsx-props-no-spreading
            {...register(
              'accountNumber',
              {
                required:
            { value: true, message: ' ' },
              },
            )}
          />
          {errors.accountNumber ? (
            <Error>{errors.accountNumber.message}</Error>
          )
            : (
              <DefaultMeesage>(-)   8 </DefaultMeesage>
            )}
          {bankStore.isTransferFail && `${bankStore.errorCode}`.startsWith(1) ? (
            <Error>
              <p>{bankStore.errorMessage}</p>
            </Error>
          ) : (null)}
        </Element>
        <Element>
          <Label htmlFor="input-amount">&#40;&#41; &#58;</Label>
          <Field
            id="input-amount"
            type="number"
            // eslint-disable-next-line react/jsx-props-no-spreading
            {...register('amount', {
              required:
            { value: true, message: ' ' },
            })}
          />
          {errors.amount ? (
            <Error>{errors.amount.message}</Error>
          ) : (
            <DefaultMeesage>
                :
              {' '}
              {numberFormat(bankStore.amount)}
              
            </DefaultMeesage>
          )}
          {bankStore.isTransferFail && `${bankStore.errorCode}`.startsWith(2) ? (
            <Error>
              <p>{bankStore.errorMessage}</p>
            </Error>
          ) : (null)}
        </Element>
        <Element>
          <Label htmlFor="input-name">    &#58;</Label>
          <Field
            id="input-name"
            // eslint-disable-next-line react/jsx-props-no-spreading
            {...register('name', {
              required:
            { value: true, message: '      ' },
            })}
          />
          {errors.name ? (
            <Error>{errors.name.message}</Error>
          ) : <DefaultMeesage>      </DefaultMeesage>}
          <PrimaryButton type="submit" disabled={bankStore.isTransferProcessing}></PrimaryButton>
          {bankStore.isTransferProcessing ? (
            <p> ...</p>
          ) : null}
          {bankStore.isTransferSuccess ? (
            <p>  !</p>
          ) : null}
        </Element>
      </form>
    </Container>
  );
}
